.cvContainer {
  padding: 48px 75px 44px 80px;
  background-color: #fff;
}

.personSection {
  padding-bottom: 19px;
  display: grid;
  grid-template-columns: 2.3fr 1.2fr;
}
.personInfo h2 {
  margin-bottom: 17px;
  /* font-family: "Helvetica Neue"; */
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 42px;
  color: #f93b1d;
}
.xazi {
  width: 100px;
  height: 2px;
  background-color: red;
  margin-top: 22px;
}

.contact {
  margin-bottom: 34px;
}
.contact p {
  display: flex;
  align-items: center;
  column-gap: 12px;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #1a1a1a;
}

.experienceItem {
  padding-bottom: 32px;
}

.education h2,
.experience h2,
.aboutMe h4 {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #f93b1d;
  margin-bottom: 15px;
}
.education h2 {
  margin-bottom: 0;
}

.aboutMe p {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  text-transform: lowercase;
  color: #000000;
}

.personImg {
  width: 246px;
  height: 246px;
  border-radius: 133px;
  background: #474747;
}

.personImg img {
  width: 100%;
  height: 100%;
  border-radius: 133px;
  object-fit: cover;
}

.experience {
  border-top: 1px solid #c8c8c8;

  padding: 24px 0 0 0;
}
.education {
  padding: 32px 0 32px 0;
  margin-bottom: 150px;
  border-top: 1px solid #c8c8c8;
}
.educationItem {
  padding: 22px 0;
}
.borderEducation {
}

.jobDate,
.eduDate {
  margin-bottom: 16px;
}

.jobDate h4,
.eduDate h4 {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #1a1a1a;
  margin-bottom: 7px;
}

.jobDate p,
.eduDate p {
  font-style: italic;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #919191;
}

.expDescription,
.educationDescription {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  text-transform: lowercase;
  color: #000000;
}
